import React, { useState, useEffect, useContext } from "react";
import "semantic-ui-css/semantic.min.css";
import "./App.css";
import bocadillo from "./images/bocadillo.png";
import chocolate from "./images/chocolate.png";
import coca from "./images/coca.png";
import cena from "./images/cena.png";
import imageCompression from "browser-image-compression";
import { storage } from "./firebase/firebase";

import { Carrito } from "./Contexto";
import {
  Container,
  Segment,
  Header,
  Dropdown,
  Table,
  List,
  Popup,
  Image,
  Label,
  Button,
  Icon,
  Modal,
  Dimmer,
  Loader,
  Input,
  Divider,
} from "semantic-ui-react";
import { GoogleSpreadsheet } from "google-spreadsheet";

function currencyFormat(num) {
  return "$" + num.toFixed(2).replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
}

function FechaStringShort(fecha) {
  var ye = new Intl.DateTimeFormat("en", { year: "numeric" }).format(fecha);
  var mo = new Intl.DateTimeFormat("en", { month: "numeric" }).format(fecha);
  var da = new Intl.DateTimeFormat("en", { day: "2-digit" }).format(fecha);
  var fechaStr = `${ye}-${mo < 10 ? "0" + mo : mo}-${da}`;
  return fechaStr;
}

function compare(a, b) {
  // Use toUpperCase() to ignore character casing
  const fechaA = a.Cliente;
  const fechaB = b.Cliente;

  let comparison = 0;
  if (fechaA < fechaB) {
    comparison = -1;
  } else if (fechaA > fechaB) {
    comparison = 1;
  }
  return comparison;
}

function removeDuplicates(myArr, prop) {
  return myArr.filter((obj, pos, arr) => {
    return arr.map((mapObj) => mapObj[prop]).indexOf(obj[prop]) === pos;
  });
}

class TravellerCollection extends Array {
  sum(key) {
    return this.reduce((a, b) => a + (b[key] || 0), 0);
  }
}

function ExcelDateToJSDate(serial) {
  var utc_days = Math.floor(serial - 25568);
  var utc_value = utc_days * 86400;
  var date_info = new Date(utc_value * 1000);

  var fractional_day = serial - Math.floor(serial) + 0.0000001;

  var total_seconds = Math.floor(86400 * fractional_day);

  var seconds = total_seconds % 60;

  total_seconds -= seconds;

  var hours = Math.floor(total_seconds / (60 * 60));
  var minutes = Math.floor(total_seconds / 60) % 60;

  var date_data = new Date(
    date_info.getFullYear(),
    date_info.getMonth(),
    date_info.getDate(),
    0,
    0,
    0
  );

  return date_data;
}

export default function Clientes() {
  //fechas
  let today = new Date();
  var dd = today.getDate();
  var mm = today.getMonth() + 1;
  var yyyy = today.getFullYear();
  var hora = today.getHours();
  var minutos = today.getMinutes();
  dd < 10 ? (dd = "0" + dd) : (dd = today.getDate());
  mm < 10 ? (mm = "0" + mm) : (mm = today.getMonth() + 1);
  if (hora < 10) hora = "0" + hora;
  if (minutos < 10) minutos = "0" + minutos;
  let date = yyyy + "-" + mm + "-" + dd;
  let fecha = date + " " + hora + ":" + minutos;
  let wd = today.getDay();
  wd === 0 ? (wd = 7) : (wd = wd);
  const [dataSemana, setDS] = useState();
  let startDate = new Date(today);
  startDate.setDate(startDate.getDate() - 15);
  const [inicio, setInicio] = useState(FechaStringShort(startDate));
  const [final, setFinal] = useState(FechaStringShort(today));
  //fechas
  const [sort_key, setSortKey] = useState("Fecha");
  const [sort_order, setSortOrder] = useState("descending");
  const [ttlAmt, setTtlamt] = useState(0);
  const [pagado, setPagado] = useState(0);
  const [loading, setLoading] = useState(false);
  const [open, setOpen] = useState(false);
  const [data, setData] = useState();
  const [buscar, setBuscar] = useState("");
  const [categoria, setCat] = useState("Snacks");
  const [cliente, setCliente] = useState("");
  const [listaClientes, setListaClientes] = useState();
  const dummyImg = "https://react.semantic-ui.com/images/wireframe/image.png";
  const [previewImg, setPreviewImg] = useState(dummyImg);
  const [activeItem, setActiveItem] = useState("");
  const allInputs = { imgUrl: "" };
  const [imageAsFile, setImageAsFile] = useState("");
  const [imageAsUrl, setImageAsUrl] = useState(allInputs);
  const [subir, setSubir] = useState(false);
  const [saving, setSaving] = useState(false);
  const [payamt, setPayamt] = useState(0);
  const [pago, setPago] = useState("Efectivo");
  const [saldoInicial, setSaldoInicial] = useState(0);
  const [saldoFinal, setSaldoFinal] = useState(0);

  const categorias = [
    { name: "Snacks", src: bocadillo, categ: "Snacks" },
    { name: "Dulces", src: chocolate, categ: "Dulces" },
    { name: "Bebidas", src: coca, categ: "Bebida" },
    { name: "Otros", src: cena, categ: "Otros" },
  ];
  const sacardata = async () => {
    setLoading(true);
    var data = [];
    let datas = [];
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[0];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    const encabezado = await sheet.headerValues;
    let lastr = rows.length + 1;
    var j, i;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      data = [];
      for (i = 0; i <= cols - 1; i++) {
        window["Celda" + j + i] = sheet.getCell(j, i).value;
        if (encabezado[i] === "Fecha") {
          window["Celda" + j + i] = FechaStringShort(
            ExcelDateToJSDate(window["Celda" + j + i])
          );
        }
        data[`${encabezado[i]}`] = window["Celda" + j + i];
      }
      datas.push(data);
    }
    setData(datas);
    console.log(datas);
    setLoading(false);
  };

  async function handleImageUpload(event, id) {
    console.log(id);
    if (event.target.files && event.target.files[0]) {
      const imageFile = event.target.files[0];
      var reader = new FileReader();
      var preview = document.getElementById(id);
      reader.onload = function (e) {
        preview.setAttribute("src", e.target.result);
      };

      reader.readAsDataURL(event.target.files[0]);
      const options = {
        maxSizeMB: 1,
        maxWidthOrHeight: 1920,
        useWebWorker: true,
      };
      try {
        const compressedFile = await imageCompression(imageFile, options);
        setImageAsFile((imageFile) => compressedFile);
      } catch (error) {
        console.log(error);
      }
      setSubir(true);
    } else {
      setSubir(false);
      setImageAsFile("");
      setImageAsUrl("");
      preview = document.querySelector(id);
      preview.setAttribute("src", "");
      var archivoSelect = document.querySelector("#anexoImg");
      archivoSelect.value = "";
    }
  }
  const registrarPago = async () => {
    if (!cliente) {
      alert("Escriba su Nombre Completo");
    } else {
      setSaving(true);
      const creds = require("./samsnacks.json"); // the file saved above
      const doc = new GoogleSpreadsheet(
        "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
      );
      await doc.useServiceAccountAuth(creds);
      await doc.loadInfo(); // loads document properties and worksheets
      const sheet = doc.sheetsById[0];
      const pedidoid = Date.now() + cliente.split(" ").join("");
      var i;
      const newData = await sheet.addRow({
        Fecha: fecha,
        Cliente: cliente,
        Product_Id: "PAGO",
        Product_Name: pago,
        Cantidad: 0,
        Monto: 0,
        Payment_Method: "Contado",
        Estatus: "Completado",
        Entrega: "NA",
        //Tel: tel,
        NumPedido: pedidoid,
        Payment_Amt: payamt,
      });
      alert(`Pago Realizado, su numero de pedido es ${pedidoid}`);
      setCliente();
      setData();
      setSaving(false);
    }
  };

  const handleFireBaseUpload = (e) => {
    if (subir) {
      if (imageAsFile === "") {
        console.error(
          `not an image, the image file is a ${typeof imageAsFile}`
        );
      }
      const uploadTask = storage
        .ref(`/images/${imageAsFile.name}`)
        .put(imageAsFile);
      uploadTask.on(
        "state_changed",
        (snapShot) => {
          //takes a snap shot of the process as it is happening
          console.log(snapShot);
        },
        (err) => {
          //catches the errors
          console.log(err);
        },
        () => {
          // gets the functions from storage refences the image storage in firebase by the children
          // gets the download url then sets the image from firebase as the value for the imgUrl key:
          storage
            .ref("images")
            .child(imageAsFile.name)
            .getDownloadURL()
            .then((fireBaseUrl) => {
              setImageAsUrl((prevObject) => ({
                ...prevObject,
                imgUrl: fireBaseUrl,
              }));
              console.log(fireBaseUrl);
            });
        }
      );
    }
  };

  const registrar = async () => {
    setSaving(true);
    console.log(activeItem);
    const creds = require("./samsnacks.json"); // the file saved above
    const doc = new GoogleSpreadsheet(
      "1usbvWq_DBOLN4j1wvK-l7QquTHe9yc9PqFRXhhOmZk0"
    );
    await doc.useServiceAccountAuth(creds);
    await doc.loadInfo(); // loads document properties and worksheets
    const sheet = doc.sheetsById[711905343];
    const rows = await sheet.getRows();
    const cols = await sheet.columnCount;
    let lastr = rows.length + 1;
    var j;
    await sheet.loadCells({
      // GridRange object
      startRowIndex: 0,
      endRowIndex: lastr,
      startColumnIndex: 0,
      endColumnIndex: cols,
    });
    for (j = 1; j < lastr; j++) {
      if (sheet.getCell(j, 0).value === activeItem.id) {
        const precio = sheet.getCell(j, 2);
        const imagen = sheet.getCell(j, 3);
        const inventario = sheet.getCell(j, 5);
        if (imageAsUrl.imgUrl) {
          imagen.value = imageAsUrl.imgUrl;
        }
        precio.value = parseFloat(activeItem.price);
        inventario.value = parseFloat(activeItem.disponible);
        await sheet.saveUpdatedCells();
        break;
      }
    }
    setImageAsUrl(allInputs);
    setSaving(false);
  };
  function ordenar(a, b) {
    // Use toUpperCase() to ignore character casing
    const fechaA = a[sort_key];
    const fechaB = b[sort_key];

    let comparison = 0;
    if (fechaA < fechaB) {
      sort_order === "ascending" ? (comparison = -1) : (comparison = 1);
    } else if (fechaA > fechaB) {
      sort_order === "ascending" ? (comparison = 1) : (comparison = -1);
    }
    return comparison;
  }

  useEffect(() => {
    !data && sacardata();
  }, [data]);

  useEffect(() => {
    subir && handleFireBaseUpload();
  }, [subir]);

  useEffect(() => {
    !data && sacardata();
    if (data) {
      var temp = removeDuplicates(
        data
          .sort(function ordenar(a, b) {
            // Use toUpperCase() to ignore character casing
            const fechaA = a["Cliente"];
            const fechaB = b["Cliente"];

            let comparison = 0;
            if (fechaA < fechaB) {
              comparison = -1;
            } else if (fechaA > fechaB) {
              comparison = 1;
            }
            return comparison;
          })
          .filter((item) => {
            return item.Fecha >= inicio && item.Fecha <= final;
          }),
        "Cliente"
      );
      let templista = [];
      var i;
      for (i = 0; i < temp.length; i++) {
        templista.push({
          key: temp[i].Cliente,
          value: temp[i].Cliente,
          text: temp[i].Cliente,
        });
      }
      setListaClientes(templista);
    }
  }, [data, inicio, final]);

  useEffect(() => {
    if (data) {
      let montoInicial = data
        .filter((item) => {
          return (
            item.Cliente.toLowerCase().includes(
              cliente ? cliente.toLowerCase() : cliente
            ) && item.Fecha < inicio
          );
        })
        .reduce((r, d) => r + d.Monto, 0);
      let pagoInicial = data
        .filter((item) => {
          return (
            item.Cliente.toLowerCase().includes(
              cliente ? cliente.toLowerCase() : cliente
            ) && item.Fecha < inicio
          );
        })
        .reduce((r, d) => r + d.Payment_Amt, 0);
      setSaldoInicial(montoInicial - pagoInicial);
      let montoFinal = data
        .filter((item) => {
          return (
            item.Cliente.toLowerCase().includes(
              cliente ? cliente.toLowerCase() : cliente
            ) &&
            item.Fecha >= inicio &&
            item.Fecha <= final
          );
        })
        .reduce((r, d) => r + d.Monto, 0);
      let pagoFinal = data
        .filter((item) => {
          return (
            item.Cliente.toLowerCase().includes(
              cliente ? cliente.toLowerCase() : cliente
            ) &&
            item.Fecha >= inicio &&
            item.Fecha <= final
          );
        })
        .reduce((r, d) => r + d.Payment_Amt, 0);
      setTtlamt(montoFinal);
      setPagado(pagoFinal);
      setSaldoFinal(montoInicial - pagoInicial + montoFinal - pagoFinal);
    }
  }, [data, cliente, inicio, final]);

  return (
    <div className="Prueba">
      <Dimmer active={loading || saving} page>
        <Loader>{saving ? "Saving" : "Loading"}</Loader>
      </Dimmer>
      <Modal open={open}>
        <Modal.Header>{cliente && cliente.toUpperCase()}</Modal.Header>
        <Modal.Content image>
          <Modal.Description>
            <Input
              labelPosition="right"
              type="number"
              placeholder="Amount"
              onChange={(e) => {
                setPayamt(e.target.value);
              }}
            >
              <Label basic>$</Label>
              <input
                type="number"
                onChange={(e) => {
                  setPayamt(e.target.value);
                }}
              />
            </Input>
            <Button
              content={pago}
              icon={pago === "Efectivo" ? "dollar" : "exchange"}
              onClick={() => {
                setPago(pago === "Efectivo" ? "Wire" : "Efectivo");
              }}
            ></Button>
          </Modal.Description>
        </Modal.Content>
        <Modal.Actions>
          <Button color="red" onClick={() => setOpen(false)}>
            Cancelar
          </Button>
          <Button
            content="Guardar"
            labelPosition="right"
            icon="checkmark"
            onClick={() => {
              registrarPago();
              setOpen(false);
            }}
            positive
          />
        </Modal.Actions>
      </Modal>
      <Input
        id="inicio"
        label="Inicio"
        size="mini"
        type="date"
        defaultValue={inicio}
        onChange={(e) => {
          setInicio(e.target.value);
        }}
      />
      <Input
        id="final"
        label="Final"
        size="mini"
        type="date"
        defaultValue={final}
        onChange={(e) => {
          setFinal(e.target.value);
        }}
      />
      <Dropdown
        style={{ marginTop: "1%" }}
        placeholder="Clientes"
        fluid
        clearable
        search
        selection
        options={listaClientes}
        onChange={(e, { value }) => {
          setCliente(value);
        }}
      />
      <div
        style={{
          height: "80%",
          backgroundColor: "lemonchiffon",
          marginTop: "1%",
          overflowY: "auto",
          padding: "1%",
        }}
      >
        {cliente && (
          <Button
            color="green"
            icon="money"
            floated="right"
            onClick={() => setOpen(true)}
          ></Button>
        )}
        <p>Saldo Inicial {cliente ? currencyFormat(saldoInicial) : null}</p>
        <p>Consumo {cliente ? currencyFormat(ttlAmt) : null}</p>
        <p>Pago {cliente ? currencyFormat(pagado) : null}</p>
        <p>Saldo Final {cliente ? currencyFormat(saldoFinal) : null}</p>
        {data && (
          <Segment className="contenido">
            <Table basic="very" celled collapsing>
              <Table.Header>
                <Table.Row>
                  <Table.HeaderCell
                    onClick={() => {
                      setSortKey("Fecha");
                    }}
                  >
                    Fecha
                    <Icon
                      name={
                        sort_order === "ascending"
                          ? "triangle up"
                          : "triangle down"
                      }
                      onClick={() => {
                        setSortKey("Fecha");
                        sort_order === "ascending"
                          ? setSortOrder("descending")
                          : setSortOrder("ascending");
                      }}
                    ></Icon>
                  </Table.HeaderCell>
                  <Table.HeaderCell
                    onClick={() => {
                      setSortKey("Product_Name");
                    }}
                  >
                    Producto
                    <Icon
                      name={
                        sort_order === "ascending"
                          ? "triangle up"
                          : "triangle down"
                      }
                      onClick={() => {
                        setSortKey("Product_Name");
                        sort_order === "ascending"
                          ? setSortOrder("descending")
                          : setSortOrder("ascending");
                      }}
                    ></Icon>
                  </Table.HeaderCell>
                  <Table.HeaderCell
                    onClick={() => {
                      setSortKey("Cliente");
                    }}
                  >
                    Cliente
                    <Icon
                      name={
                        sort_order === "ascending"
                          ? "triangle up"
                          : "triangle down"
                      }
                      onClick={() => {
                        setSortKey("Cliente");
                        sort_order === "ascending"
                          ? setSortOrder("descending")
                          : setSortOrder("ascending");
                      }}
                    ></Icon>
                  </Table.HeaderCell>
                  <Table.HeaderCell
                    onClick={() => {
                      setSortKey("Cantidad");
                    }}
                  >
                    Cantidad
                    <Icon
                      name={
                        sort_order === "ascending"
                          ? "triangle up"
                          : "triangle down"
                      }
                      onClick={() => {
                        setSortKey("Cantidad");
                        sort_order === "ascending"
                          ? setSortOrder("descending")
                          : setSortOrder("ascending");
                      }}
                    ></Icon>
                  </Table.HeaderCell>
                  <Table.HeaderCell
                    onClick={() => {
                      setSortKey("Monto");
                    }}
                  >
                    Monto
                    <Icon
                      name={
                        sort_order === "ascending"
                          ? "triangle up"
                          : "triangle down"
                      }
                      onClick={() => {
                        setSortKey("Monto");
                        sort_order === "ascending"
                          ? setSortOrder("descending")
                          : setSortOrder("ascending");
                      }}
                    ></Icon>
                  </Table.HeaderCell>
                  <Table.HeaderCell
                    onClick={() => {
                      setSortKey("Payment_Amt");
                    }}
                  >
                    Pago
                    <Icon
                      name={
                        sort_order === "ascending"
                          ? "triangle up"
                          : "triangle down"
                      }
                      onClick={() => {
                        setSortKey("Payment_Amt");
                        sort_order === "ascending"
                          ? setSortOrder("descending")
                          : setSortOrder("ascending");
                      }}
                    ></Icon>
                  </Table.HeaderCell>
                </Table.Row>
              </Table.Header>

              <Table.Body>
                {data
                  .filter((item) => {
                    return (
                      item.Cliente.toLowerCase().includes(
                        cliente ? cliente.toLowerCase() : cliente
                      ) &&
                      item.Fecha >= inicio &&
                      item.Fecha <= final
                    );
                  })
                  .sort(function ordenar(a, b) {
                    // Use toUpperCase() to ignore character casing
                    const fechaA = a["Fecha"];
                    const fechaB = b["Fecha"];

                    let comparison = 0;
                    if (fechaA < fechaB) {
                      comparison = 1;
                    } else if (fechaA > fechaB) {
                      comparison = -1;
                    }
                    return comparison;
                  })
                  .sort(ordenar)
                  .map((item) => (
                    <Table.Row>
                      <Table.Cell>{item.Fecha}</Table.Cell>
                      <Table.Cell>{item.Product_Name}</Table.Cell>
                      <Table.Cell>{item.Cliente}</Table.Cell>
                      <Table.Cell>{item.Cantidad}</Table.Cell>
                      <Table.Cell>{currencyFormat(item.Monto)}</Table.Cell>
                      <Table.Cell>
                        {currencyFormat(item.Payment_Amt)}
                      </Table.Cell>
                    </Table.Row>
                  ))}
              </Table.Body>
            </Table>
          </Segment>
        )}
      </div>
    </div>
  );
}
